import { graphql } from "gatsby";

import React from "react";
import CommonHead from "../layout/Head";
import SuccessfulPurchase from "../layout/pages/SuccessfulPurchase";
import MainSectionWrapper from "../layout";


const PageTemplate = (props) => {
    return (
      <MainSectionWrapper>
        <SuccessfulPurchase />
      </MainSectionWrapper>
    )
};

export function Head(props) {
    const data = {
        page: {
            name: "Crochet Nest - Thanks for your purchase",
        }
    };

    return <CommonHead {...props} data={data} />;
}

export default PageTemplate;